new Vue({
  data(){
    return {
      items: [
        {
          name: '',
          amount: '',
          note: '',
        }
      ],
      work: [
        'Ремонт',
        'Монтаж',
      ],
      OpenIndicator: {
        render: createElement => createElement('span', ''),
      },
      executor : [
        'ООО "Контрагент по ремонту ТС',
        'ООО "Контрагент по ремонту КПП',
      ]
    }
  },
  watch: {
    'items': {
      handler (newValue, oldValue) {
        newValue.forEach((item) => {
          item.name + item.amount + item.note
        })
      },
      deep: true
    },
  },
  methods: {
    AddItem(){
      this.items.push({
        name: '',
        amount: '',
        note: '',
      })
    },
    removeItem(){
      this.items.splice(this.items, 1)
    }

  },
  
}).$mount("#table-service-app")

